import styled, {
  keyframes
} from 'styled-components';

export const Wrapper = styled.div `
  position: relative;
  z-index: 1;
`;

export const Bound = styled.div`
	width: 90%;
	height: 72vh;
  margin: 0 auto;
  max-width: 436px;
  min-height: 200px;

`;

export const Text = styled.div `
	line-height: 3rem;
  /* animation: ${window} 750ms 250ms forwards; */
  /* opacity: 0; */
  border-radius: 4px;
  color: snow;
  font-size: 2rem;
  overflow: hidden;
  padding: 12px;
  position: relative;
  transform: translateZ(0);
	will-change: transform, opacity;
	opacity: 0.6;

  @media screen and (max-width: 769px) {
    :root {
      --window-overlap: -64px;
      --window-underlap: -32px;
    }
    .window {
      width: 85vw;
    }
  }

  @media screen and (max-width: 421px) {
    .window {
      padding: 4px;
      max-height: 235px;
    }
  }
`;

const window = keyframes`
  0% {
    opacity: 0;
    transform: translateY(32px) scale(0.9);
  }
  75% {
    opacity: 1;
  }
  100% {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
`;

export const Title = styled(Text)`
	font-size: 2.4rem;
	font-weight: 700;
	opacity: 1;
	border-bottom: 1px solid #eaeaea;
`;



